import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store =new Vuex.Store({
    state:{
        logined:false,
        loginName:'',//
        accesstoken:'',//token
        loginedMoreMenus:{
          center:"",
          msg:"",
          logout:''
        },
        unLoginedMoreMenus:{
          login:""
        },
        isShowMoreMenus:false,
        moreMenus:{
          login:''
        },
        topics:[
          {name:'all',desc:''},
          {name:'good',desc:''},
          {name:'share',desc:''},
          {name:'ask',desc:''},
          {name:'job',desc:''},
          {name:'test',desc:''},
        ]
    },
    getters:{  
        moreMen(state){
        // statestorestate
        if(state.logined){
          return state.loginedMoreMenus
        }
        return state.unLoginedMoreMenus;
      },
    },
    mutations:{
        mutationsisShowMoreMenus:function(state,payload){
            // 
            state.isShowMoreMenus=payload;
        },                                        
        mutationIsLogined:function(state){
          state.logined=true;
        },
        // 
        mutationLonginname:function(state,payload){
          state.loginName=payload;
        },
         // accesstoken
         mutationAccesstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 
        mutationLogout(state){
          state.logined=false;
          state.logiName='';
          state.accesstoken='';
        }
    },
    actions:{}
})
export default store;


